body {
  --color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link:hover {
  color: var(--color);
}

.is-active {
  color: var(--color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
